@import "../../global.scss";
.portfolio{
    background-color: $mainColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;

    h1{
        font-size: 50px;
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

       
    }
    .container{
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item {
            width: 250px;
            height: 200px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 1s ease;
            cursor: pointer;

            h3{
                position: absolute;
                font-size: 20px;
                color: white;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: white;
                img{
                    opacity: 0.5;
                    z-index: 0;
                }
            }
        }
    }
}